<template>
  <div id='hangzhou-page'>
    <el-row :span="8">
      <el-card shadow="always" class = "first-card">
        <div>
          <div class="date">{{nowDate}}</div>
          <div class="time">{{nowTime}}</div>
        </div>
        <el-divider></el-divider>
        <div id="name-box">
          <span class="name">张* </span><el-button type="text" class="show">显示</el-button>
        </div>
        <div>
          <span class="address">浙商银行股份有限公司</span>
        </div>
        <el-image
          style="width: 60%; height: 60%; margin-top: 5px;"
          :src="require('@/assets/hangzhou.jpg')"
          :fit="fit">
        </el-image>
      </el-card>
    </el-row>
    <el-row :span="8">
      <el-card shadow="always" class = "second-card">
        <el-card shadow="never" class="nucleic-acid">
          <el-row>
            <el-col :span="8">
              <span style="font-size: 35px">48</span>
            </el-col>
            <el-col :span="16" style="padding-left: 10px">
            <span style="font-size: 10px;">
              小时内<br/>
              <span style="font-size: 20px;">阴性</span>
            </span>
            </el-col>
          </el-row>
          <el-row>
            <el-button round  class= "nucleic-acid-btn">
              核酸检测
              <span class="nucleic-acid-icon">></span>
            </el-button>
          </el-row>
        </el-card>
        <el-divider direction="vertical" style="height: 100px; width: 1px"></el-divider>
        <el-card shadow="never" class="key-areas" >
          <el-row>
            <el-col :span="11">
              <el-image
                style="width: 100%; height: 100%; "
                :src="require('@/assets/key-areas.jpg')"
                :fit="fit">
              </el-image>
            </el-col>
            <el-col :span="13" style="margin-top: 10px;">
            <span style="font-size: 15px;">
             疫情重点<br/>地区核验
            </span>
            </el-col>
          </el-row>
          <el-row>
            <span style="font-size: 10px; color: #8a8a8a">
              您于7天内未到访<br/>疫情重点地区所在市
            </span>
          </el-row>
        </el-card>
      </el-card>
      <el-card shadow="always" class = "third-card">
        <el-col :span="16" style="text-align: left;">
          <el-row >
            <el-col :span="4">
              <el-image
                style="width: 100%; height: 100%;"
                :src="require('@/assets/trip.jpg')"
                :fit="fit">
              </el-image>
            </el-col>
            <el-col :span="20">
              <span style="font-size: 15px; margin-left: 5px">行程卡</span>
            </el-col>
          </el-row>
          <el-row>
            <span style="font-size: 10px; color: #8a8a8a; margin-left: 2px">
              查看7天是否到过中高风险地区
            </span>
          </el-row>
        </el-col>
        <el-col :span="8">
          <el-button round class="trip-btn">详情</el-button>
        </el-col>
      </el-card>
      <el-image
        style="width: 50%; height: 50%; margin-top: 5px"
        :src="require('@/assets/footer.jpg')"
        :fit="fit">
      </el-image>
    </el-row>
  </div>
</template>

<script>
    export default {
        name: "hangzhou",
        data () {
            return{
                nowDate: "",//当前日期
                nowTime: "",//当前时间
            }
        },
        methods:{
            currentTime() {
                setInterval(this.formatDate, 500);
            },
            formatDate() {
                let date = new Date();
                let month = date.getMonth() + 1; // 月
                let day = date.getDate(); // 日
                day = day < 10 ? "0" + day : day; // 如果只有一位，则前面补零
                let hour = date.getHours(); // 时
                hour = hour < 10 ? "0" + hour : hour; // 如果只有一位，则前面补零
                let minute = date.getMinutes(); // 分
                minute = minute < 10 ? "0" + minute : minute; // 如果只有一位，则前面补零
                let second = date.getSeconds(); // 秒
                second = second < 10 ? "0" + second : second; // 如果只有一位，则前面补零
                this.nowDate = `${month}月${day}日`;
                this.nowTime = `${hour}:${minute}:${second}`;
            }
        },
        mounted() {
            this.currentTime();
        },
        // 销毁定时器
        beforeDestroy() {
            if (this.formatDate) {
                clearInterval(this.formatDate); // 在Vue实例销毁前，清除时间定时器
            }
        }
    }
</script>

<style scoped>
  #hangzhou-page{
    width:100%;
    height:100%;
    position:fixed;
    background-size:cover;
    background: linear-gradient(
      to bottom,
      #46a761,
      #46a761 20%,
      white 20%,
      white 100%
    );
  }
  >>>.el-card__body{
    padding: 0;
  }
  .first-card{
    border-radius: 10px;
    margin: 18px 20px 5px 20px;
    padding: 20px 10px 0px 10px;
  }
  .second-card{
    border-radius: 10px;
    margin: 5px 20px 5px 20px;
    padding: 15px 20px;
  }
  .third-card{
    border-radius: 10px;
    margin: 5px 20px 5px 20px;
    padding: 15px 20px;
  }
  .el-divider{
    margin: 8px 0;
    background: 0 0;
    border-top: 1px solid #E6EBF5;
  }
  .date{
    transform: scale(1, 1.1);
    /*font-family: "Microsoft YaHei";*/
    font-size: 35px;
    font-weight: 600;
    color: #46a761;
    letter-spacing: 1px;
  }
  .time{
    /*font-family: "Microsoft YaHei";*/
    font-size: 50px ;
    font-weight: 600;
    color: #46a761;
  }
  #name-box{
    margin-top: -10px;
    margin-bottom: -5px;
    text-align:left;
  }
  .name{
    font-weight: bold;
  }
  .show{
    margin-left: 10px;
    font-size: 5px;
  }
  .address{
    margin-top: 5px;
    font-weight: bold;
  }
  .nucleic-acid {
    width: 45%;
    height: 120px;
    background: linear-gradient(to top right, #539de4 0%, #59b0c4 25%, #5ec99d 100%);
    color: white;
    float: left;
    display:flex;
    justify-content: center;
    align-items: center;
  }
  .key-areas{
    width: 45%;
    height: 120px;
    float: right;
  }
  .el-card{
    border: none;
  }
  .nucleic-acid-btn{
    background-color: rgba(0, 0, 0, 0.0);
    color: white;
    padding: 5px 10px 5px 10px;
    font-size: 10px;
    letter-spacing: 1px;
    margin-top: 10px;
  }
  .nucleic-acid-icon{
    border-radius: 50%;
    background-color: white;
    color: #a4a3b7;
    padding: 0 2px;
  }
  .trip-btn{
    color: #4f8fe1;
    border: 1px solid #4f8fe1;
    padding: 5px 20px 5px 20px;
    margin: 10%;
  }
</style>


